<template>
  <div class="card">
    <!-- Card header -->
    <div class="card-header" v-if="showTitle">
      <!-- Title -->
      <h5 class="h3 mb-0">Student Practice</h5>
    </div>
    <!-- Card body -->
    <div class="card-body">
      <div
        class="timeline timeline-one-side"
        data-timeline-content="axis"
        data-timeline-axis-style="dashed"
      >
        <div v-for="event in events" :key="event.title" class="timeline-block">
          <span class="timeline-step" :class="`badge-${event.type}`">
            <i :class="event.icon"></i>
          </span>
          <div class="timeline-content">
            <div class="d-flex justify-content-between pt-1">
              <div>
                <span
                  class="text-muted text-md tk-font-color-black2 tk-font-weight-bold"
                  >{{ event.title }}</span
                >
              </div>
              <div class="text-right">
                <span
                  class="text-muted text-md tk-font-weight-bold"
                  :class="{
                    'tk-font-color-red': event.time === 0,
                    'tk-font-color-main': event.time > 0,
                  }"
                >
                  <!-- <i class="fas fa-clock mr-1"></i> -->
                  {{ event.time }} hrs
                </span>
              </div>
            </div>
            <div
              class="text-md tk-font-color-gray tk-px-margin-top-px20 tk-px-margin-bottom-px10"
              v-if="event.selfStudy.length > 0"
            >
              <i class="fas fa-gem text-lg tk-layout-inline-block tk-px-margin-right-px10 tk-font-color-card8"></i>
              <span class="tk-layout-inline-block">Self Study</span>
            </div>
            <template v-for="(item, index) in event.selfStudy">
              <div
                class="text-md tk-font-color-gray tk-px-margin-top-px10"
                :key="item.name + index"
              >
                <img
                  class="tk-px-width-px22 tk-layout-inline-block tk-px-margin-right-px10"
                  src="/img/icons/tk/icon.log.png"
                />
                <span class="tk-layout-inline-block">{{ item.name }}</span>
              </div>
            </template>
            <div
              class="text-md tk-font-color-gray tk-px-margin-top-px20 tk-px-margin-bottom-px10"
              v-if="event.assignment.length > 0"
            >
              <i class="fas fa-music text-lg tk-layout-inline-block tk-px-margin-right-px10" style="color: #ccabd8"></i>
              <span class="tk-layout-inline-block">Assignment</span>
              <div class="text-right" style="float: right">
                <span
                  class="text-muted text-md tk-font-weight-bold tk-font-color-red" v-if="!isComplete(event.assignment)">
                  Uncompleted
                </span>
                <span
                  class="text-muted text-md tk-font-weight-bold tk-font-color-main" v-else>
                  Completed
                </span>
              </div>
            </div>
            <template v-for="(item, index) in event.assignment">
              <div
                class="text-md tk-font-color-gray tk-px-margin-top-px10"
                :key="item.name + index"
              >
                <i class="far fa-circle text-lg tk-layout-inline-block tk-px-margin-right-px10 tk-font-color-red" v-if="!item.complete"></i>
                <i class="fas fa-check-circle text-lg tk-layout-inline-block tk-px-margin-right-px10 tk-font-color-lightGray" v-else></i>
                <span class="tk-layout-inline-block">{{ item.name }}</span>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    data: {
      default: []
    },
    showTitle: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      events: []
    };
  },
  mounted() {
    let self = this
    self.events = self.data
  },
  methods: {
    isComplete(items) {
      let flag = true
      items.some(item => {
        if (!item.complete) {
          flag = false
        }
      })
      return flag
    }
  }
};
</script>
